<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.back()" />

    <van-search
      autofocus
      :placeholder="recmdItem_index"
      show-action
      clearable
      v-model.trim="userInput"
      @search="toSearch(userInput)"
    >
      <template #action>
        <div @click="toSearch(userInput)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="historyList.length">
      <div class="title">
        <span>最近搜索</span>
        <van-icon name="delete-o" size="16" @click="clearHistory" />
      </div>
      <div class="list">
        <div
          class="list-item"
          v-for="(item, index) in historyList"
          :key="index"
          @click="toSearch(item)"
        >
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getLocalSearchHistory, setLocalSearchHistory } from '@/utils/storage.js'

export default {
  name: 'SearchIndex',

  data () {
    return {
      // searchResult -> searchIndex : 搜索框value
      userInput: this.$store.state.search.value_index,
      historyList: getLocalSearchHistory(),
      // 推荐条目由SearchBanner -> $store -> searchIndex传递过来
      recmdItem_index: this.$store.state.search.recmdItem_store
    }
  },

  methods: {
    toSearch (value) {
      // 用户不输入时默认搜索推荐的商品
      if (!value) {
        value = this.recmdItem_index
      }
      if (!value) {
        this.$toast('请输入要搜索的内容')
        return
      }
      // 查找之前是否搜索过相同内容，有则删除
      const index = this.historyList.indexOf(value)
      if (index !== -1) {
        this.historyList.splice(index, 1)
      }
      // 跳转页面
      this.$router.replace(`/searchresult?search=${value}`)
      // 修改value_index
      this.$store.commit('search/setIndexValue', value)
      // 最新搜索放在最前
      this.historyList.unshift(value)
      // 将最新的历史记录本地化
      setLocalSearchHistory(this.historyList)
    },

    clearHistory () {
      this.historyList = []
      setLocalSearchHistory([])
    }

  },

  created () {

  }

}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
